import { Link } from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

import  './aboutSection.css'


const AboutSection = () => {
    
    return (
        <div className="about-section">
            <div className="title-block">ABOUT</div>
            <Tabs>
                <TabList>
                    <Tab>JANE</Tab>
                    <Tab>ALICE</Tab>
                    <Tab><Link to="/about" className="link-more-about-us">MORE ABOUT US</Link></Tab>
                </TabList>
                <TabPanel>
                    <div className="photo-block">
                        <img src="images/girl.png" alt=""/>
                    </div>
                    <div className="text-block">Hi, I am Jane, I write travel guides, posts and create hiking trails so you can plan your trip to places you've never heard of.</div> 
                </TabPanel>
                <TabPanel>
                    <div className="photo-block">
                        <img src="images/girl_2.png" alt=""/>
                    </div>
                     <div className="text-block">Hi, I am Alice, I like traveling, talking with different people, making photos, so you may find  in my posts something interesting for you</div>
                </TabPanel>
                <TabPanel>
                    <div className="more-about-us-block">
                        <Link to="/about" className="link-more-about-us">MORE ABOUT US</Link>
                    </div>
                </TabPanel>
            </Tabs>
        </div>
    )
}

export default AboutSection