import React from 'react'

import {Route} from 'react-router'

import MainPage from './MainPage/MainPage'
import BlogPage from './BlogPage/BlogPage'
import AboutPage from './AboutPage/AboutPage'
import ContactPage from './ContactPage/ContactPage'
import CategoryPage from './CategoryPages/CategoryPage'
import PostPage from './PostPage/PostPage'
import LikedPage from './LikedPage/LikedPage'

const Main = ({
    productsLikeState,
    addLike,
    removeLike,
}) => {
    return (
        <>
            <Route path="/react-blog-pr/" render={() => ( 
                <MainPage
                    productsLikeState={productsLikeState}
                    addLike={addLike}
                    removeLike={removeLike}
                />
            )}/>
                <Route path="/travel" render={() => (
                    <CategoryPage 
                        productsLikeState={productsLikeState}
                        addLike={addLike}
                        removeLike={removeLike}
                        categoryName="TRAVEL"
                        image–°ategory="react-blog-pr/images/travel.png"
                    />
                )}/>
                <Route path="/fashion" render={() => (
                    <CategoryPage
                        productsLikeState={productsLikeState}
                        addLike={addLike}
                        removeLike={removeLike} 
                        categoryName="FASHION"
                        image–°ategory="react-blog-pr/images/fashion.png"
                    />
                )}/>
                <Route path="/lifestyle" render={() => (
                    <CategoryPage 
                        productsLikeState={productsLikeState}
                        addLike={addLike}
                        removeLike={removeLike}
                        categoryName="LIFESTYLE"
                        image–°ategory="react-blog-pr/images/lifestyle.png"
                    />
                )}/>
                <Route path="/food" render={() => (
                    <CategoryPage
                        productsLikeState={productsLikeState}
                        addLike={addLike}
                        removeLike={removeLike}
                        categoryName="FOOD"
                        image–°ategory="react-blog-pr/images/food.png"
                    />
                )}/>
                <Route path="/inspiration" render={() => (
                    <CategoryPage
                        productsLikeState={productsLikeState}
                        addLike={addLike}
                        removeLike={removeLike}
                        categoryName="INSPIRATION"
                        image–°ategory="react-blog-pr/images/inspiration.png"
                    />
                )}/>
                <Route path="/photography" render={() => (
                    <CategoryPage
                        productsLikeState={productsLikeState}
                        addLike={addLike}
                        removeLike={removeLike}
                        categoryName="PHOTOGRAPHY"
                        image–°ategory="react-blog-pr/images/photography.png"
                    />
                )}/>
            <Route path="/blog" render={() => ( 
                <BlogPage 
                    productsLikeState={productsLikeState}
                    addLike={addLike}
                    removeLike={removeLike}
                /> 
            )}/>
            <Route path="/likedposts" render={() => ( 
                <LikedPage
                    productsLikeState={productsLikeState}
                /> 
            )}/>
            <Route path="/about" component={AboutPage}/>
            <Route path="/contact" component={ContactPage}/>
            <Route path="/post/:id" render={({match}) => (
                <PostPage
                    productsLikeState={productsLikeState}
                    addLike={addLike}
                    removeLike={removeLike}
                    match={match}
                />           
            )}/>
        </>
    )
}

export default Main
