import React, { Component } from 'react';

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';

import blogPosts from '../../BlogPage/BlogPosts/blogPosts'
import "./sliderBlock.css"
import { Link } from 'react-router-dom';


class SliderBlock extends Component {
    render() {

        return (
            <div className="slider-block">
                <Carousel showThumbs={false} autoPlay={true} interval={5000} infiniteLoop={true}>
                    { 
                        blogPosts.slice(7, 10).map(({
                            id,
                            imageSlider,
                            category,
                            title,
                        }) => ( 
                            <div key={id} > 
                                <img src={imageSlider}/> 
                                <div className="legend">
                                    <div className="slider-category">{category}</div>
                                    <div className="slider-title">{title}</div>
                                    <Link to={`/post/${id}`} className="slider-link">READ MORE</Link>
                                </div>
                            </div>
                        ) )
                    } 
                </Carousel>
            </div>
        );
    }
}


export default SliderBlock