import React from 'react'

import './postDirections.css'
import directions from './directions'
import { Link } from 'react-router-dom'


const PostDirections = () => {
    return (
        <div className="posts-derictions">
            {
                directions.map(({
                    id,
                    name,
                    image,
                    link,
                }) => (
                    <div key={id} className="post-direction-item">
                        <Link to={link}>
                            <img src={image} alt="" />
                            <div className="post-direction-box">
                                <div className="post-direction-bg">
                                    <div className="post-direction-title">
                                        {name}
                                    </div>
                                </div>
                            </div>
                        </Link>
                    </div>
                ))

                }
        </div>
    )
}

export default PostDirections