import React, {Component} from 'react'


import './postListItem.css'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom';
import PostMainPhoto from '../../../../../Components/PostMainPhoto/PostMainPhoto'

class PostListItem extends Component {
    render() {
        
        const {
            id,
            image,
            date,
            category,
            author,
            title,
            text,
            isLiked,
            addLike,
            removeLike,
        } = this.props;

        return (
            <div className="post-item">
                <PostMainPhoto
                    image={image}
                />
                <div className="post-description">
                    <div className="post-header">
                        <div className="post-info">
                            <div className="post-date">{date}</div>
                            <div className="post-category">{category}</div>
                            <div className="post-author">by {author}</div>
                            <div onClick={() => isLiked ? removeLike(id) : addLike(id)}
                            className="post-like">
                                {isLiked ? <div className="post-like liked"></div> : <div className="post-like"></div>}                        
                            </div>
                        </div>
                        <div className="post-title">{title}</div>
                    </div>
                    <div className="post-text">{text}</div>
                    <Link to={`/post/${id}`} className="link-read-more">Read More</Link>
                </div>
            </div>
        )
    } 
}


PostListItem.propTypes = {
    title:PropTypes.string.isRequired,
    text:PropTypes.string.isRequired,
    category:PropTypes.string.isRequired,
    author:PropTypes.string.isRequired,
}
PostListItem.defaultProps = {
    image:"/images/no-image.png"
}



export default PostListItem