import React from 'react'

import './categoryPages.css'


import CategorysPostItem from './CategoryPostsItem'
import blogPosts from '../../Main/BlogPage/BlogPosts/blogPosts'


const CategoryPage = ({
    categoryName,
    image–°ategory,
    productsLikeState,
    addLike,
    removeLike,
}
) => {
    return (
        <>
            <div className="sticky-picture">
                    <img src={image–°ategory} alt=""/>
            </div> 
            <main className="main">
                <div className="container">
                    <div className="category-row">
                        <div className="category-title">{categoryName}</div>
                        <div className="categ-posts-list">
                        {  
                        
                            blogPosts.filter(item => item.category === categoryName).map(({
                                id,
                                image,
                                date,
                                category,
                                author,
                                title,
                                text,
                            }) => (
                                <CategorysPostItem 
                                    key={id}
                                    id={id}
                                    image={image}
                                    date={date}
                                    category={category}
                                    author={author}
                                    title={title}
                                    text={text}
                                    isLiked={productsLikeState[id]}
                                    addLike={addLike}
                                    removeLike={removeLike}
                                />
                            ))
                            
                        }
                        </div>
                    </div>
                </div>
            </main>
        </>
        
    )
}


export default CategoryPage