import React from 'react'

import './postHeader.css'

const PostHeader = ({
    id,
    date,
    category,
    author,
    title,
    isLiked,
    addLike,
    removeLike,
}) => {
    return (
        <div className="post-header">
            <div className="post-header-info">
                <div className="post-header-date">{date}</div>
                <div className="post-header-category">{category}</div>
                <div className="post-header-author">by {author}</div>
                <div onClick={() => isLiked ? removeLike(id) : addLike(id)}
                className="post-like">
                    {isLiked ? <div className="post-like liked"></div> : <div className="post-like"></div>}                        
                </div>
            </div>
            <div className="post-header-title">{title}</div>
        </div>
    )
}

export default PostHeader