import React, {Component} from 'react'

import PropTypes from 'prop-types'
import './categoryPages.css'
import { Link } from 'react-router-dom';
import PostHeader from '../../../Components/PostHeader/PostHeader';
import PostMainPhoto from '../../../Components/PostMainPhoto/PostMainPhoto';

class CategoryPostItem extends Component {
    render() {
        
        const {
            id,
            image,
            date,
            category,
            author,
            title,
            text,
            isLiked,
            addLike,
            removeLike,
        } = this.props;

        return (
            <>
                <div className="categ-post-item">
                    <PostMainPhoto
                        image={image}/>
                    <div className="categ-post-description">
                        <PostHeader
                            id={id}
                            date={date}
                            category={category}
                            author={author}
                            title={title}
                            isLiked={isLiked}
                            addLike={addLike}
                            removeLike={removeLike}
                        />
                        <div className="categ-post-text">{text}</div>
                        <Link to={`/post/${id}`} className="link-read-more">Read More</Link>
                    </div>
                </div>


            </>
        )
    } 
}



CategoryPostItem.propTypes = {
    title:PropTypes.string.isRequired,
    category:PropTypes.string.isRequired,
    author:PropTypes.string.isRequired,
}
CategoryPostItem.defaultProps = {
    image:"/images/no-image.png"
}



export default CategoryPostItem