import React from 'react'
import { Link } from 'react-router-dom'

import logoF from './logo_f.png'
import './logoFooter.css'

const LogoFooter = () => {
    return (
        <div className="logo-footer">
            <Link to="/react-blog-pr/">
                 <img src={logoF} alt=""/>
            </Link>
        </div>        
    )
}

export default LogoFooter