import React, { Component } from 'react'

import instagramCards from './instagramCards'
import "./instagramBlock.css"

class InstagramBlock extends Component {
    constructor(props) {
        super(props);
        this.state = {
            properties:instagramCards.properties,
            property:instagramCards.properties[6]
        }
    }
    nextProperty = () => {
        const newIndex = this.state.property.index+1;
        this.setState({
            property: instagramCards.properties[newIndex]
        })
    }
    prevProperty = () => {
        const newIndex = this.state.property.index-1;
        this.setState({
            property: instagramCards.properties[newIndex]
        })
    }

    render () {
        const {properties, property} = this.state;
        return (
            <div className="instagram-block">
                <div className="inst-title">INSTAGRAM</div>
                <div className={`cards-slider active-slide-${property.index}`}>
                    <div className="cards-slider-wrapper" style={{
                        'transform':`translateX(-${property.index*(100/properties.length)}%)`
                    }}>

                        {
                            properties.map(({
                                id,
                                index,
                                image,
                            }) => ( 
                                <div key={id} id={`card-${index}`} className="card">
                                    <img src={image} alt="Photo"/>
                                </div>
                            ))

                        }

                        
                    </div>
                </div>
                <div className="button-box">
                    <button
                        onClick={() => this.prevProperty()}
                        disabled={property.index === 0}
                    >PREV PHOTO</button>
                    <button
                        onClick={() => this.nextProperty()}
                        disabled={property.index === 
                        instagramCards.properties.length-1}
                    >NEXT PHOTO</button>
                </div>

            </div>
        )
    }
}

export default InstagramBlock