import React, { useState }  from 'react'

import './postsList.css'
import PostListItem from './PostListItem'
import blogPosts from '../../../BlogPage/BlogPosts/blogPosts'



const PostsList = ({
    productsLikeState,
    addLike,
    removeLike,
}) => {
     
    const [sliceNumber, setSliceNumber] = useState(4)

    const addSliceNumber  = () => {
        setSliceNumber((value) => (
            value + 4
        ))
    }

    return (
        <div className="posts-list">
            {
                blogPosts.slice(0, sliceNumber).map(({
                    id,
                    image,
                    date,
                    category,
                    author,
                    title,
                    text,
                }) => (
                    <PostListItem 
                        key={id}
                        id={id}
                        image={image}
                        date={date}
                        category={category}
                        author={author}
                        title={title}
                        text={text}
                        isLiked={productsLikeState[id]}
                        addLike={addLike}
                        removeLike={removeLike}
                        
                    />
                ))

                }
            <div className="btn-more-posts">
                <button 
                    onClick={() => addSliceNumber()}
                    disabled={sliceNumber >= blogPosts.length}

                >MORE POSTS</button>
            </div>
        </div>
    )
}


export default PostsList