import React from 'react'

import './blogPostsList.css'
import  blogPosts from './blogPosts'
import { Link } from 'react-router-dom';
import PostHeader from '../../../../Components/PostHeader/PostHeader';
import PostMainPhoto from '../../../../Components/PostMainPhoto/PostMainPhoto';


const BlogPostsList = ({
    productsLikeState,
    addLike,
    removeLike,
}) => {

    return (
        <div className="blog-posts-list">
            { 
                blogPosts.map(({
                    id,
                    image,
                    date,
                    category,
                    author,
                    title,
                }) => (
                    <div key={id} className="blog-post-item">
                        <PostMainPhoto
                            image={image}
                        />
                        <div className="blog-post-description">
                            <PostHeader
                                date={date}
                                category={category}
                                author={author}
                                title={title}
                                id={id}
                                isLiked={productsLikeState[id]}
                                addLike={addLike}
                                removeLike={removeLike}
                            />
                            <Link to={`/post/${id}`} className="link-read-more">Read More</Link>
                        </div>
                    </div>
                ))  
            }
            
        </div>
    )
}


export default BlogPostsList