        title:"List road trips for every decade of your life",
        text:"There‚Äôs nothing quite like being out on the open road. It‚Äôs freeing to pack up the car and take on the role of the pilot, stopping on a whim or making a detour when something catches your eye.",
        fullTextPartOne:"<p>There‚Äôs nothing quite like being out on the open road. It‚Äôs freeing to pack up the car and take on the role of the pilot, stopping on a whim or making a detour when something catches your eye.</p><p>According to a 2019 survey by MMGY Global, a marketing firm specializing in travel, hospitality, and entertainment, the great American road trip is enjoying a comeback, with a 64 percent increase since 2015.</p><p>The best part about road trips is that there‚Äôs something for everyone ‚Äî a transcontinental or multiday road trip isn‚Äôt just for college grads or retirees, there‚Äôs a route for travelers in every decade of their life.</p><p>To get you started on your next great American road trip, we‚Äôve researched where you should go and what you should do during each decade of your life ‚Äî because what you‚Äôll like when you‚Äôre a 20-something will be vastly different than what will satisfy you when you‚Äôre a 60-something. Of course, these routes are only meant to get the ball rolling. Like any good road trip pilot, feel free to add in stops and side trips that suit you to create your own personalized version of these epic road trips.</p><h2>In your 20s: Pacific Coast Highway</h2><p>Technically, you can make the coastal drive from northern California to San Diego in around 10 hours, but this is not the sort of road trip you‚Äôll want to rush. The Pacific Coast Highway (also known as Highway 1) is one of the most scenic drives in the world, so roll down the windows, enjoy the views, and keep on top of your Instagram story.</p><p>The route leads travelers right along the continental United States‚Äô westernmost coast, where ‚Äúpull over the car, we need a photo‚Äù sunsets are the norm and castles, sea lions, and pebbly beaches are waiting to be discovered.</p><p> You can make the drive down California‚Äôs Highway 1 in a day, but for a true road trip, we recommend starting your adventure in Olympia, Washington and ending in San Diego, California, crossing three coastal states along the way. In Olympia, stock up on road trip essentials before heading west until you hit Highway 101 (which eventually leads to California‚Äôs Highway 1).</p><p> Before you reach the Oregon border, swing by Cape Disappointment State Park. It‚Äôs a great place to camp (or rent a yurt for the night), but you can also get out and stretch your legs on the 1.2-mile Cape Disappointment Trail or the 0.45-mile Benson Beach Trail. If you‚Äôre lucky, you may spot an orca in the distance.</p>",
        fullTextPartTwo:"<p>In Oregon, swing by the Devil‚Äôs Punchbowl State Natural Area. During low tide, you can explore the tide pools and pack along a picnic, but you‚Äôll want to get to higher ground as the tide comes in and the ocean starts slamming and swirling into the natural carved-out bowl. For great photo ops, head to Cape Perpetua Scenic Area or take a hike and catch the sunset at the Samuel H. Boardman State Scenic Corridor.</p><p>When you cross the border into California, it doesn‚Äôt take long to link up with Highway 1, the official start of the Pacific Coast Highway.</p><h2>In your 30s: The Loneliest Road</h2><p>Drive through Redwood National and State Parks ‚Äî home of the tallest trees on Earth ‚Äî and post up on the black-sand beach in King Range National Conservation Area‚Äôs Shelter Cove. Further down the road, visit the historic lighthouse in Point Reyes National Seashore before hitting San Francisco‚Äôs Golden Gate Bridge.</p><p>Continuing east, head through the Rocky Mountains, crossing over the Continental Divide. Grab a hearty lunch and more road trip provisions in Salida, Colorado before heading into eastern Colorado and the start of the Great Plains, where you‚Äôll get a healthy dose of Americana landscapes ‚Äî  small towns, railroad tracks, and water towers.</p><h2>In your 40s: Grand Canyon to Yellowstone to Mount Rushmore</h2><p>This 1,290-mile route takes around 20 hours and includes stops at the Grand Canyon, Yellowstone National Park, and Mount Rushmore. Providing the perfect blend of outdoor activity, family car time, and American history, it makes for the ultimate week-long family road trip.</p><p>Your journey kicks off in Flagstaff, Arizona, just over an hour from Grand Canyon National Park‚Äôs South Rim, which is open year-round. In the park, cruise along Desert View Drive and burn off some energy while climbing the 85 steps to the top of the Desert View Watchtower, where you can enjoy 360-degree views of the canyon.</p><h2>In your 50s: The Oregon Trail</h2><p>Entering your 50s marks a transition ‚Äî your kids are becoming more independent and your retirement and health are top of mind. It‚Äôs a shift that warrants plenty of time to reflect and enjoy the beauty of the U.S. And what better way to achieve that than a transcontinental road trip? The Oregon Trail is a beautiful, roughly 2,000-mile route that connects Missouri to Oregon.</p><p>Just like the pioneers in the mid-1800s, you can start your expedition in Independence, Missouri, making your way west ‚Äî although at a much faster pace ‚Äî toward Oregon. You‚Äôll pass through the beautiful, expansive Great Plains before stopping in Sioux City, Iowa to get a dose of culture at the Sioux City Art Center. In Nebraska, make a stop at the Ashfall Fossil Beds State Historical Park, a protected area full of fossils.<h2>In your 60s: Route 66</h2><p>The romance of Route 66 is only amplified by driving the aptly named road in your 60s ‚Äî even better if you happen to be 66. The route runs from Chicago to Los Angeles, passing through the heart of the U.S. and some notorious roadside stops. Jam ‚ÄúRoute 66‚Äù by the Nat King Cole Trio as you zoom by the best of Americana ‚Äî from giant neon signs to tiny towns and vacant truck stops.</p><p>Before it became the Route 66 we know today, there was the transcontinental National Old Trails Road, and years later, around World War II, it was a main thoroughfare through small towns across the Midwest and Southwest. During the Great Depression, farmers displaced by the Dust Bowl traveled to California along the route. Today, you‚Äôll get a taste of Route 66‚Äôs long history as you pass old-timey diners and motels and plenty of roadside attractions that provide a healthy dose of nostalgia.</p><h2>In your 70s and Beyond: The Atlantic Coast</h2><p>Your 70s are the perfect decade to hit the road and see the sights. The Atlantic Coast road trip starts in New York City and ends with a tranquil drive across the Overseas Highway to Key West ‚Äî with plenty of coastal wilderness, beautiful beach resorts, and old fishing villages along the way. The trip is just under 2,000 miles and much of it runs along ‚Äî or near ‚Äî the Atlantic Ocean.</p><p>While in North Carolina, stop to stretch your legs in Wilmington‚Äôs downtown. For a taste of nostalgia, visit Myrtle Beach, South Carolina, which is home to a boardwalk that‚Äôs lined with rides and saltwater taffy shops. If you‚Äôre up for a round of golf, or some more beach time, head to Hilton Head Island, then drop down to Savannah, Georgia.</p>",
