        title:"The best places for extreme jumping",
        text:"People have invented a lot in the search for a source of thrill. We will tell you about which places on the planet are especially good for getting off the ground in this article.",
        fullTextPartOne:"<p>People have invented a lot in the search for a source of thrill. We will tell you about which places on the planet are especially good for getting off the ground in this article. Paragliders, bungee jumping (bungee), skydiving or base jumping (different types of parachuting) replaced the person‚Äôs wings.</p><p>I'm glad that You with me, so let see what places on the planet are especially good for taking off from the earth and attractive so many tourists.</p><h2>1. Paragliding</h2><p>A paraglider is a non-powered manned aircraft with a fixed-wing, inflated by air. Flying on it is not very fast, but it is a good opportunity for the paraglider to appreciate the views passing below. Where is it especially nice to soar on a paraglider?</p><h3>Bazaruto Archipelago, Mozambique</h3><p>This archipelago, almost untouched by civilization, is located 750 km from the African continent. Here are white sandy beaches and crystal clear water. Paragliding enthusiasts come here to fly all year round.</p><h3>Tanyang, South Korea</h3><p>Two and a half hours drive from Seoul ‚Äì and in front of you is the Asian capital of paragliding. The popular tourist city with beautiful surrounding landscapes is so fond of extreme sports that it even has two runways for paragliders and hang gliders.</p><h3>Dune at Pyla, Gironde, France</h3><p>The largest sand dune and one of the most famous places for paragliding in Western Europe. The sky above the dune in Pyla, located on the shore, is ideal for maneuvering paragliding due to the constant wind blowing from the sea. But due to these special weather conditions, the dune is more suitable for experienced extreme sportsmen, not beginners.</p><h2>2. Bungee jumping</h2><p>Bungee jumping is a popular attraction, which in our country is often called a bungee. Participants are tied to a long rubber rope with which they jump down. A spectacular jump requires a sufficient height and, of course, a beautiful view.</p><h3>Kawarau Bridge,  Queenstown, New Zealand</h3><p>The attraction opened in 2003 and quickly became popular ‚Äì almost a million people managed to jump during this time. The fall height is small ‚Äì 43 meters and they land after jumping directly into the turquoise water of the Kawarau River.</p><h3>Dam on the Versazka River, Ticino, Switzerland</h3><p>On this dam, the moment of James Bond‚Äôs fall in the GoldenEye film was filmed. Here they fly in flight from a concrete wall of a dam 220 meters high. Ride and jump can be ordered from specialists.</p><h2>3. Base jumping</h2><p>Base jumping is an extreme sport that uses a special parachute for jumping from rocks, mountains, and high-rise buildings. The lower the object, the more dangerous the landing.</p><h3>Angel Falls, Venezuela</h3><p>The highest (979 meters) waterfall in the world and one of the most beloved base jumpers sites. The flight is accompanied by the roar of a powerful stream of water falling down the steep cliffs and disappearing into the tropical thickets. Bejumping is allowed here by law and special tours are regularly arranged at the waterfall.</p><h3>Navajo Bay, Zakynthos, Greece</h3><p>One of the most beautiful beaches in Greece and the most popular base jumping place in Europe. Jump here from a cliff 200 meters high. The freefall lasts from two to five seconds, and then ‚Äì landing in soft sand. There are regularly held rallies of base jumpers.</p>",
        fullTextPartTwo:"<h2>4. Skydiving</h2><p>Skydiving (lit. diving into the sky) is a type of parachuting, where free fall is extended due to the fact that the parachute is opened as close to the ground as possible.</p><h3>Dubai, UAE</h3><p>Skydive Dubai is one of the most popular parachute clubs in the world. This official organization, opened with the permission of the sheik, whose son is a fan of skydiving, attracts tourists from all over the world to jump in a beautiful area with a view of the famous man-made island of Palm Jumeirah.</p><h3>Lake Bled, Slovenia</h3><p>The quiet beautiful lake surrounding the island of Bled in Slovenia is one of the most popular places for skydiving in all of Europe due to its picturesque views and easy access.</p><h3>Byron Bay, Australia</h3><p>On the famous Australian beach there is an opportunity to jump with a parachute, looking at the easternmost point of Australia and Byron Bay lighthouse. After the flight, tourists are given four hours of surfing lessons.</p> <img scr=`/react-blog-pr/images/parasute_2.png`>",
