        text:"Looking at these photos, it's difficult to imagine that everything is a creation of nature. We invite you to see this for yourself by planning a trip to one of the places on our list.",
        fullTextPartOne:"<p>Looking at these photos, it's difficult to imagine that everything is a creation of nature. We invite you to see this for yourself by planning a trip to one of the places on our list.</p><h2>1. Spotted Lake, British Columbia, Canada</h2><p>Spotted Lake has long been revered by the native Okanagan (Syilx) people and it‚Äôs easy to see why they think of it as sacred. In the summer the water of the lake evaporates and small mineral pools are left behind, each one different in colour to the next. The unique lake can be viewed on Highway 3, northwest of the small town of Osoyoos, although visitors are asked not to trespass on tribal land. If you're looking to explore more waters, read our selection of 20 breathtaking lakes around the world to inspire your travels.</p><h2>2. The Giant's Causeway, Northern Ireland</h2><p>Sixty million years ago a huge volcanic eruption spewed out a mass of molten basalt, which then solidified and contracted as it cooled, creating the cracks that can be seen today. There are an estimated 37,000 polygon columns at this World Heritage Site, so geometrically perfect that local legend has it they were created by a giant. It's a firm contender for one of the weirdest places on earth. If you're heading further afield to find some natural wonders, read our selection of the best volcanoes around the world.</p><h2>3. Pamukkale, Turkey</h2><p>A remarkable UNESCO World Heritage Site in southwest Turkey, a visit to Pamukkale (Cotton Palace) also takes in the ancient ruins of Hierapolis, the once great city that was built around it. Water cascades from natural springs and down the white travertine terraces and forms stunning thermal pools perfect for a quick dip. We recommend joining a small group tour to have a guide with you. You may have never heard of Pamukkale in Turkey until now, and you may have never known about these facts about Turkey either.</p><h2>4. The Tianzi mountains, China</h2><p>Found in the northwest of Hunan Province in China, these staggering limestone pinnacles are covered in lush greenery and often shrouded in mist. A cable car goes as far as Huangshi village and from here there are plenty of trails to take in the breathtaking views of Tianzi ('son of heaven'). Unsurprisingly they are the inspiration for the floating mountains in the blockbuster movie Avatar. If you're looking to explore more of what the country has to offer, read about our things not to miss in China.</p>",
        fullTextPartTwo:"<h2>5. Socotra Island, Yemen</h2><p>Separated from mainland Africa more than six million years ago, this remote island looks like the set of a sci-fi film. Socotra‚Äôs incredible and unique biodiversity means that there are plants and trees here not found anywhere else in the world ‚Äì particularly bizarre are the ancient and twisted dragon‚Äôs blood tree and the bulbous bottle tree.</p><h2>6. Chocolate Hills of Bohol Island, the Philippines</h2><p>Bohol‚Äôs 1700-odd conical hills dot the middle of the island in the Philippines. they range in height but are so regular in shape that they could be mistaken for being man-made. However, according to UNESCO they are the uplift of coral deposits and a result of rainwater erosion. The hills only earn their 'chocolate' nickname in the dry season when the foliage goes from lush green to brown.Consider combining a trip to the Chocolate Hills with a visit to the Tarsier Sanctuary and a lunch cruise along the Loboc River. Bohol Island is just one of our favourite islands in the Philippines. Make sure to head to Olango Island to find migratory birds and try diving on Panago Island.</p><h2>7.  Red Beach, Panjin, China</h2><p>Very cool and very weird, this beach is covered in a type of seaweed called Sueda, which turns bright red in autumn. Thirty kilometres southwest of Panjin, these tidal wetlands are an important nature reserve for migrating birds. Only a small section of the beach is open to the public, but it can be explored via a wooden walkway that stretches out to sea. Red Beach may just be one of China's best secret beaches. Head here quick, along with these other secret beaches around the world before the crowds arrive.</p><h2>8. Plain of Jars, Laos</h2><p>Shrouded in myth, megalithic stone jars are scattered across Xieng Khouang Province in groups from one to one hundred. A working theory is that the huge cylindrical jars were used in ancient funeral ceremonies, though local legend has it that the jars were used to brew rice wine for giants. In the 1960s Northern Laos was subject to a massive aerial bombardment by the USA and it‚Äôs only been relatively recently that some areas have been cleared and declared safe for visitors.</p><h2>9. Goblin Valley State Park, Utah, USA</h2><p>No, this is not Mars but an uninhabited valley 216 miles southeast of Salt Lake City in Utah. Soft sandstone has, for many years, been eroded by wind and water to form strange pinnacles or hoodoos that some think resemble goblins. The eerie landscape is only about a mile across and two miles long and it‚Äôs well worth exploring the marked trails to get up close to the bizarre formations.Also relatively unknown is the fact that you can go on a spectacular canyoneering adventure in the Valley of Goblins. Looking for more outdoor adventures in Utah? Why not stargaze in Antelope Island State Park?</p><h2>10.  Glass Beach, California, USA</h2><p>This glittering sea glass beach in California is a remarkable side effect of years of rubbish being dumped on the beach. It wasn‚Äôt until the 1960s that this was stopped and by then the sea was full of everything from electrical appliances to bottles and cans. Over time, the waves broke everything down into colourful pebbles and the beach became a major tourist attraction ‚Äì now ironically under threat because visitors are taking home the glass. It's not all glass on the beaches in the state! Here are the best 10 beaches in California if you're looking for sun, sea and sand.</p>",
