        text:"Today we will be recharged with positive energy from the delightful photography of Ashraful Arefin, a talented 31-year-old photographer and digital artist based in Dhaka, Bangladesh. He focuses mainly on fine arts, macro photography, flowers, toys and still lifes. Ashfull was always very interested in drawing and painting, he wanted to be an artist from childhood.",
