        title:"Top 10 delicious dessert trends for 2021",
        text:"We take a look at the biggest trends in desserts, treats and puddings that will be satisfying your sweet tooth in the coming year.",
        fullTextPartOne:"<p>We take a look at the biggest trends in desserts, treats and puddings that will be satisfying your sweet tooth in the coming year.</p><p>This year, Baileys has served up its second Treat Report, curated with top global treat makers and lovers, looking at the delicious trends destined to take off in 2021.</p><h2>1. Going oat oat</h2><p>Whether you‚Äôre blitzing them into your breakfast smoothie, creating Acai bowls, cupping your oat lattes in the cold, or fuelling yourself with a flapjack on-the-go, oats have certainly taken on a decadent makeover this year. Oat-based milk chocolates, ice-creams, yoghurts and even double cream are coming our way and we can‚Äôt wait. And to kickstart the ‚Äònew-year-new-me‚Äô, veganuary vibes, oats are gluten-free, sustainable and a locally-grown cereal.</p><h2>2. Cacao-loco</h2><p>Know the difference between your cocoa and your cacao? Cacao pulp, the bit you don‚Äôt see as it‚Äôs generally discarded when making chocolate, is a sweet and fruity flesh. You can also expect to see it as a flavourful alternative to coconut water, topped with eye catching edible flowers for the perfect non-alcoholic refresher.</p><h2>3. Asian eats</h2><p>Japan is a haven for whimsical treats that will put a smile on your face and we‚Äôll be seeing a massive influx in towering treats including giant fluffy cheesecakes and wobbly, fat, pillow-like pancakes that can be topped with fruit, creme patissiere or powdered sugar. Post-brunch nap incoming! But, if you‚Äôre like us and just need a pick-me-up after a weekend brunch of pancakes and coffee, the Taiyaki fish will give you the boost you need. These soft waffle-like, fish-shaped cones, stuffed to the gills with ice-cream, sprinkles and sauces are definitely the catch of the week.</p><h2>4. Spread the love</h2><p>Savoury spreads are getting a makeover as they turn into sweet treats. Add a touch of opulence to toast with lashings of Sloe Gin spreads and Champagne butters to turn your humble morning loaf into something of luxury.</p><h2>5. Extra sweet(s)</h2><p>They‚Äôve been around for a few years, but in 2021, some classics get a gloriously adventurous treating make-over as the macaron and Danish pastry get an upgrade. Introducing the Macaron Ice Cream Sandwich and the Nougat Crown, with all the muted tones and flavours getting set aside for over the top colour and tastes‚Ä¶.. already building up a sizeable following in Los Angeles.</p>",
        fullTextPartTwo:"<h2>6. Gourmet glow-up</h2><p>Edible algae that will find its way into our favourite cocktails, giving them an incredible other-worldly glow-in-the-dark appearance that will be hard to ignore. Forget the seaweed facemasks ‚Äì will someone pass us a Glowtini?</p><h2>7. As light as air</h2><p>On the coattails of the whipped Dalgona coffee, this treating trend is only growing in popularity in 2021. Air will be whipped, injected and stuffed into everything from ice cream to candy floss, alleviating us from the heaviness of 2020 and giving a light texture that leaves us craving more.</p><h2>8. Clashing combos</h2><p>Here at Good Homes, we‚Äôve been a big fan of clashing prints and interiors. But whilst we‚Äôre used to sweet and salty recipes, next year it‚Äôs ramping up into something even more indulgent and creative with ice cream fries, Danish dessert pizza, potato snacks covered in milk chocolate and caramel flavour to name but a few.</p><h2>9. Moreish Mochi</h2><p>Next year there‚Äôs going to be a mochi for everyone, from ice cream with a mochi textured top layer, mochi pancake mixes, mochi doughnuts and even savoury skewers of stuffed mochi balls all on the horizon.</p><h2>10. Self-care sipping</h2><p>Unsurprisingly, everyone has been indulging in a bit more self-care in 2020 and the self-care kick will continue. People will be indulging themselves in new botanical drinks that offer qualities like ‚Äòcalm‚Äô, ‚Äòfocus‚Äô, ‚Äòglow‚Äô and ‚Äòrest‚Äô, and new fermented beverages like hop water; a non-alcoholic drink with a slight beer or ale taste, will be bigger than ever.</p>",
