        author:"J.Stoyn",
        title:"Plisse skirts: How we wil wear them in spring 2021",
        text:"Pleating has not gone out of fashion for several years. But still, from year to year, the characteristics of trendy pleated skirts change slightly. What pleated skirts are in fashion this spring? Here are the main signs.",
        fullTextPartOne:"<p>Pleating has not gone out of fashion for several years. But still, from year to year, the characteristics of trendy pleated skirts change slightly. What pleated skirts are in fashion this spring? Here are the main signs.</p><p><span>Large folds</span>. If you are about the same age as I am, or more, you should remember the skirts that were in vogue in the mid-nineties. Wide flat pleats, folded to one side. This season, these skirts are both knee-length and floor-length.</p><p><span>Asymmetry</span> is generally one of the main skirt trends this year. Therefore, skirts ‚Äúwith a tail‚Äù or any other asymmetrical cut are welcome!</p><p><span>Smell</span> is a logical continuation of the previous asymmetry trend. Please also note that the skirt does not have to be completely pleated. Individual details are enough. It is also very interesting when several materials are combined in one skirt.</p><p>In general, the complexity of the cut is very welcome. Curly yokes, undercuts, additional details.</p>",
        fullTextPartTwo:"<p>But not all models are suitable for everyday style and not for every lifestyle. But it is much easier to fit <span>skirts with a print</span> into an image and a situation. Moreover, the trend allows you to choose the length to your liking, there is no general rule.</p>",
