import React from 'react'

import '../CategoryPages/categoryPages.css'

import blogPosts from '../BlogPage/BlogPosts/blogPosts'
import PostMainPhoto from '../../../Components/PostMainPhoto/PostMainPhoto'

import { Link } from 'react-router-dom'



const LikedPage = ({
    productsLikeState,
}) => {
    return (
        <>
            <div className="sticky-picture">
                <img src="react-blog-pr/images/liked_page_bg.png" alt=""/>
            </div> 
            <main className="main">
                <div className="container">
                    <div className="category-row">
                        <div className="category-title">LIKED POSTS</div>
                        <div className="categ-posts-list">
                        {  
                        
                            blogPosts.filter(item => 
                                productsLikeState[item.id] === true).map(({
                                id,
                                image,
                                date,
                                category,
                                author,
                                title,
                                text,
                            }) => (
                                <div key={id} className="categ-post-item">
                                    <PostMainPhoto
                                        image={image}/>
                                    <div className="categ-post-description">
                                    <div className="post-header">
                                        <div className="post-header-info">
                                            <div className="post-header-date">{date}</div>
                                            <div className="post-header-category">{category}</div>
                                            <div className="post-header-author">by {author}</div>
                                            <div className="post-like liked hover"></div>                         
                                        </div>
                                        <div className="post-header-title">{title}</div>
                                    </div>
                                        <div className="categ-post-text">{text}</div>
                                        <Link to={`/post/${id}`} className="link-read-more">Read More</Link>
                                    </div>
                                </div>

                            ))
                            
                        }
                        </div>
                    </div>
                </div>
            </main>
        </>
        
    )
}


export default LikedPage